<template>
    <!-- <main class="main w-full h-screen max-h-screen flex justify-center py-6"> -->
    <main class="game-main" :style="{'height': windowHeight+'px'}">
    <!-- <div class="w-full max-w-[850px] max-h-screen flex items-center"> -->
    <div class="main-wrapper">
      <!-- <div class="bg-amber-50 border-slate-700 border-2 rounded rounded-md shadow-md w-full"> -->
        <div class="game-view"
        
        >
        <!-- style="max-height: calc(100vh - 50px);" -->

        <GameHeader
        
        />

        <InfoContent
        v-if="actualView.type === 'info'"
        />

        <GameSections
        v-if="actualView.type === 'sections'"
        />

        <AnswersButtons
        v-if="actualView.type === 'answersButtons'"
        />

        <AnswersCheckboxes
        v-if="actualView.type === 'answersCheckboxes'"
        />

        <GameFooter 
        :footerData ="actualView.footer"
        />

      </div>
    </div>
  </main>
    
</template>
  
<script>
// import localTestData from '@/data/newData1.json'
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import InfoContent from '@/components/game/InfoContent.vue'
import GameSections from '@/components/game/GameSections.vue'
import AnswersButtons from '@/components/game/AnswersButtons.vue'
import AnswersCheckboxes from '@/components/game/AnswersCheckboxes.vue'
import GameHeader from '@/components/game/GameHeader.vue'
import GameFooter from '@/components/game/GameFooter.vue'
  
export default {
    name: 'GameMain',
    components: {
        InfoContent,
        GameSections,
        AnswersButtons,
        AnswersCheckboxes,
        GameHeader,
        GameFooter
    },
    // data() {
    //     return {
    //         windowHeight: window.innerHeight
    //     }
    // },
    computed: {
        ...mapGetters([
            'gameData',
            'stepNumber',
            'actualView',
            'windowHeight'
        ]),        
        // showParams() {
        //     return this.$route.params
        // }        
    },
    methods: {
        ...mapActions([
            'GET_DATA_FROM_API',
            'GET_DATA_FROM_LOCAL',
            'setWindowHeight'
        ]),
        onResize() {
            // this.windowHeight = window.innerHeight
            this.setWindowHeight(window.innerHeight)
        }
    },
    created(){
        // console.log(this.showParams.idTest)
        this.GET_DATA_FROM_API(1)
        // this.GET_DATA_FROM_LOCAL(localTestData)
    },
    mounted() {
        this.$nextTick(() => {
            window.addEventListener('resize', this.onResize);
        })
    },
    beforeDestroy() { 
        window.removeEventListener('resize', this.onResize); 
    },

}
</script>

